// 🏠️ OSP-0112

@import '../../modules';

section.section-osp0112{
  background-color: $bg_gray_f;
  padding-top: 4rem;
  padding-bottom: 9.6rem;
  ul.products-tos-list{
    padding-top: 2.4rem;
    margin-bottom: 7.6rem;
  }
}

// tos 구름 버전 리스트 
ul.products-tos-list{
  li{
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 5.4rem 7rem 5rem 7rem;
    margin-bottom: 2.4rem;

    text-align: left;

    background-color: white;
    box-shadow: 0px 5px 20px rgba(45, 52, 74, 0.03);
    border-radius: 2rem;
    // 제품 정보
    div.tos-product{
      span.title{
        @include style_font(900, 3.4rem, 1, $font_color-212121_grey);
      }
      span.ver{
        @include style_font(500, 2.6rem, 1, #666);
        padding-left: 8px;
      }
      p{
        padding-top: 2.4rem;
        @include style_font(400, 1.6rem, 1, #666);
      }
    }
    // 다운로드 버튼들
    div.btns-downloads{
      display: inline-flex;
      align-items: flex-end;
      a{
        margin-left: 2rem;
      }
      a.btn{
        height: 4.7rem;
      }
    }
  }
  li:last-child{
    margin-bottom: 0;
  }
}