// 🏠️ OSP-0113

@import '../../modules';

main.main-osp0113{
  padding-top: 9.6rem;
  padding-bottom: 14.8rem;
  text-align: center;
  background: $bg_gray_f;
  section.w-default{
    h3{
      @include style_font(700, 3.6rem, 1, #1f1f1f);
      padding-bottom: 4.4rem;
    }
    p{
      @include style_font(400, 1.8rem, 1, #666666);
      padding-bottom: 12.8rem;
      i.icon{
        width: 2.4rem;
        height: 2.4rem;
        background-image: none;
        margin-right: 1.2rem;
        vertical-align: middle;
      }
    }
    .img.img-github{
      display: inline-block;
      width: 48.9rem;
      height: 14rem;
      margin-bottom: 12.8rem;
      background-image: url($img_path_to_pages + 'img-OSP0112-git.png');
      background-size: cover;
    }
    a.btn{
      display: block;
      width: fit-content;
      margin: 0 auto;
      font-weight: 700;
      padding: 1.6rem 4.5rem;
      font-size: 2.2rem;
      i.icon{
        vertical-align: -0.4em;
      }
    }
  }
}