@import '../../modules';

// 공통
.custom{
  .search-table{
    margin-top: 7rem;
    margin-bottom: 2.4rem;
  }

  // 검색 결과 없을 때,
  div.no-result{
    position: relative;
    padding: 11.5rem 0;
    margin-bottom: 10.6rem;
    border-radius: 2rem;
    background-color: $bg_gray_f;
    
    .result-key{
      @include style_font(700, 2.8rem, 1, $azure);
      padding-bottom: 2rem;
    }
    span{
      @include style_font(500, 2.4rem, 1, $font_color-212121_grey);
    }
    p{
      margin-top: 6.8rem;
      @include style_font(500, 1.8rem, 2em, $font_color-797979_grey);
    }

    button span{
      text-align: center;
      color: white;
      font-size: 1.4rem;
    }
  }
}

main.main-osp7111{

  // 배경 
  .background-01{
    @include style_background-01(863px, -442px, -460px, 1298px, -175px, -770px);
    padding-top: 9.6rem;
    padding-bottom: 20.5rem;  
  }

  // 궁금한 점이 있으신가요? 
  section.products{
    h2{
      padding-bottom: 9.6rem;
    }

    ul.menu-list{ 
      display: flex;
      gap: 3.9rem;
      justify-content: center;
      z-index: 9;

      li button{
        width: 45.4rem;
        padding: 4rem 4.7rem;
        text-align: left;
        background-color: rgba($color: white, $alpha: 1.0);
        box-shadow: 0rem 1rem 4rem rgba(0, 0, 0, 0.05);
        border-radius: 3rem;

        i.icon{
          width: 10rem;
          height: 10rem;
          background-position-x: -1rem;
          // icon img
          &.icon-custom-01{background-image: url($img_path_to_icons + 'icon_CS-01.svg');}
          &.icon-custom-02{background-image: url($img_path_to_icons + 'icon_CS-02.svg');}
          &.icon-custom-03{background-image: url($img_path_to_icons + 'icon_CS-03.svg');}
        }

        h6{
          padding-top: 1.5rem;
          padding-bottom: 1.8rem;
          @include style_font(700, 2.8rem, 1.4em, #1f1f1f);
        }

        span{
          @include style_font(400, 2rem, 1.6em, #848484);
          width: 28rem;
        }
      }

      // 버튼 동작 정의 
      button{
        transition: transform 0.3s;

        &:hover{
          transform: translateY(-3rem);
        }
        &:active{
          box-shadow: 0rem 1rem 4rem rgba(0, 0, 0, 0.00);
          background-color: rgba($color: white, $alpha: 0.9);
        }
      }
    }
  }

  // 도움이 필요한 제품을 선택하세요.
  section.custom-products{
    padding-top: 9.8rem;
    padding-bottom: 11.2rem;
    // 타이틀
    h3{
      padding-bottom: 9.8rem;
    }

    ul.products-list{
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 3.9rem;

      button.btn-product{
        @include style_font(700, 3.6rem, 1, white);
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 45.4rem;
        height: 17.2rem;
        padding: 0 4rem;
        text-align: left;
        background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
        border-radius: 3rem;
      }

      i.icon_costom-ar-r{
        width: 4.6rem;
        height: 4.6rem;
      }

      // 버튼 동작 정의 
      button{
        transition: all 0.3s;
        &:hover{
          padding-right: 3rem;
          transform: translateY(-2rem);
        }
        &:active{
          opacity: 0.9;
        }
      }
    }
  }

  div.board-wrap{
    box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05);
    border-radius: 3rem;
    padding-bottom: 4.8rem;
    ul.cont-ul{
      padding: 0 4.4rem;
      margin-bottom: 4.8rem;
      div.--col1{
        padding-left: 2.6rem;
        justify-self: self-start;
      }
    }
  }

  section.custom-notice{
    padding-top: 9.6rem;
    padding-bottom: 7.6rem;
    background-color: $bg_gray_f;
    div.board-wrap.notice{
      padding-top: 1.6rem;
    }
  }

  section.custom-question{
    padding-top: 9.6rem;
    padding-bottom: 5.6rem;
    background-color: $bg_gray_f;
    div.board-wrap.question ul.cont-ul{
      padding-top: 3.6rem;
    }
  }
}