@import '../modules';

.loading{
  width: 100%;
  height: 100vh;

  display: flex;
  align-items: center;
  justify-content: center;

  .loading__container{
    -webkit-mask: url($img_path_from_layout_to_svgs + 'loading_white_font.svg');
    mask: url($img_path_from_layout_to_svgs + 'loading_white_font.svg');

    width: 22.4rem; 
    height: 3.3rem;

    .loading-tmaxos-color{
      width: 100%;
      height: 100%;
      
      background: linear-gradient(90deg, rgba(75, 165, 255, 0) 7.89%, #4BA5FF 45.72%, #4BA5FF 52.44%, rgba(75, 165, 255, 0) 93.42%);;
      background-size: 67% 100%;

      background-position-x: 0%;

      animation-name: loading;
      animation-timing-function: cubic-bezier(.65, 0,.65, 1); // ease-out ease-out
      animation-iteration-count: infinite;
      animation-duration: 3.2s;
    }
  }
}
