@import '../../modules';


main.main-osp7126{
  // 7126 레이아웃
  .width1218{
    width: 1218px;
    margin: 0 auto;
  }
  // 배경 원 규칙
  .background-01{
    @include style_background-01(1194px, -321px, -291px, 900px, -246px, -405px);
  }


  section.products-list{
    position: relative;
    padding-top: 9.6rem;
    
    h2{
      line-height: 1.6em;
      padding-bottom: 9.6rem;
      strong{
        color: $azure;
      }
    }
    ul.products{
      padding-bottom: 18rem;
      li.product{
        display: flex;
        justify-content: space-between;
        align-items: center;

        padding: 4.8rem 6.4rem 4.6rem 6.4rem;
        margin-bottom: 5rem;
        background: #FFFFFF;
        box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05);
        border-radius: 30px;
      }
      li.tmax-gr{
        // 제품 정보
        div.product-infor{
          p{
            width: 60rem;
            padding-bottom: 3.6rem;
          }
        }
        // 제품 상세 스펙
        div.tmaxgr-spec{
          @include style_font(700, 1.5rem, 1, #91989D);
          // spec-list
          ul.spec-list{
            display: inline-flex;
            flex-wrap: wrap;
            width: 75%;
            padding-left: 3.6rem;
            li{
              position: relative;
              width: 50%;
              width: 30rem;
              padding-bottom: 1.4rem;
              padding-left: 1.4rem;
              // 파란 점
              &::after{
                position: absolute;
                content: '';
                width: 5px;
                height: 5px;
                background-color: $azure;
                left: 0;
                top: 5px;
                border-radius: 50%;

              }
              // 스펙명
              span.spec-tit{
                width: 5.6rem;
                font-weight: 700;
              }
              span{
                letter-spacing: 0;
                font-weight: 400;
              }
            }
          }
        } 

      }
      // 제품 정보 
      div.product-infor{
        text-align: left;
        flex: 2 2 auto;
        h4{
          height: 4rem;
          @include style_font(700, 3rem, 1, black);
        }
        p{
          @include style_font(400, 1.8rem, 2em, #212121);
          padding-top: 1.7rem;
        }
      }
      // 버튼들 
      div.btns-download{
        // 공통 버튼 속성
        a.btn{

          display: flex;
          justify-content: space-between;
          align-items: center;
          width: 20.8rem;
          padding: 1.1rem 1.9rem 1.1rem 2.4rem;
          margin-top: 2.4rem;
        }

        // 03
        a.btn-rounded-03{
          @include style_font(500, 1.8rem, 1.2, white);
          .icon-down-svg svg{
            width: 2.4rem;
            height: 2.4rem;
            path{
              fill: white;
            }
          }
        }
        
        // 사용가이드 아이콘 
        .icon-open_in_new{
          width: 1.6em;
          height: 2.4rem;
          svg{
            width: 1.2em;
            height: 2.4rem;
            path{
              fill: white;
            }
          }
        } 
      }
    }
  }
}

